/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Oswald:200,300,600&display=swap");
@import url("https://fonts.googleapis.com/css?family=Audiowide|Cute+Font&display=swap");
* {
  box-sizing: border-box; }

a {
  text-decoration: none; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  padding: 54px 0 0;
  background: url(/img/Backgrounds/map.png);
  background-attachment: fixed;
  background-size: cover; }

#root-link {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

#root,
body,
html {
  height: 100%;
  color: #20232a; }
  #root h1,
  body h1,
  html h1 {
    font-family: 'Oswald', sans-serif; }
  #root a,
  body a,
  html a {
    text-decoration: none;
    color: white; }
  #root .MuiButton-outlined,
  body .MuiButton-outlined,
  html .MuiButton-outlined {
    border: 1px solid #20232a;
    background: rgba(0, 128, 0, 0.9);
    font-weight: 600;
    letter-spacing: 1px;
    color: #ffffff;
    padding: 10px 30px;
    font-size: 1.1rem;
    font-family: 'Oswald', sans-serif;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.8), 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12);
    align-self: center; }
    #root .MuiButton-outlined:hover,
    body .MuiButton-outlined:hover,
    html .MuiButton-outlined:hover {
      box-shadow: none;
      background: rgba(0, 128, 0, 0.7); }

@media only screen and (min-width: 769px) {
  body {
    padding: 62px 0 0; } }
